import i18n from "i18next";

import { Numeral } from "../../tools";
import { DefaultLoader } from "./default";
import type { GameCore } from "../gameCore";
import type { IGameConf, IAssetsConf, IAssetConf } from "./gameConfigurationTypes";

export class LoaderError extends Error {
	public constructor(message: string) {
		super(message);
		this.name = "LoaderError";
	}
}

export interface ILoader {
	extensions: string[];
	baseUrl: string;

	/**
	 * Add the file to load identified by the given key.
	 * @param filename the relative path of the file.
	 * @param key the key in the dictionary that identifies the file in order to retrieve using .get.
	 * @param options optionnal object that loaders can use. Typically metadata in the case of pixi.js loader.
	 */
	add(filename: string, key?: string, options?: any): void;
	/**
	 * Load all files previously added.
	 * @param callback called once all files have been loaded.
	 * @param progress called each time a file has been loaded.
	 * @param error called once if a file failed to load.
	 */
	load(
		callback: () => void,
		progress?: (filename?: string) => void,
		error?: (file: string) => void
	): void;

	/**
	 * Clear all the files to load dictionary.
	 */
	clear?(): void;
	/**
	 * Get resource/raw data from a loaded file.
	 * @param key identifier of the file to retrieve in the dictionary
	 * @param raw optionnal option to specifiy if you want the resource of the raw data.
	 * @returns the resource or the raw data of the file that has been loaded.
	 */
	get?<T>(key: string, raw?: boolean): T;
	/**
	 * Remove a file from the dictionary.
	 * @param key identifier of the file to remove.
	 */
	remove?(key: string): void;
}

export class Loader {
	/**
	 * Registers a loader which implements the interface ILoader
	 * @param loader the loader to register
	 */
	public static register(loader: ILoader): void {
		Loader._loaders.unshift(loader);
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public static get<T>(key: string, raw?: boolean): T {
		for (const loader of Loader._loaders) {
			const resource = loader.get<T>(key, raw);

			if (resource) {
				return resource;
			}
		}

		return null;
	}

	public static get loaders(): ILoader[] {
		return [].concat(Loader._loaders);
	}

	// Public members
	// Static members
	private static _loaders: ILoader[] = [];

	public onProgress: (percentage: number, loadedFile?: string) => void;
	public onError: (file: string) => void;

	public assetsPath: string = "";

	// Protected members
	protected filesCount: number = 0;
	protected _baseUrl: string;

	/**
	 * Constructor
	 */
	public constructor(public game: GameCore) {
		this.baseUrl = "./";
	}

	/**
	 * Returns the default loader
	 */
	public get defaultLoader(): DefaultLoader {
		return Loader._loaders[0] as DefaultLoader;
	}

	/**
	 * Gets the base Url of the loader
	 */
	public get baseUrl(): string {
		return this._baseUrl;
	}

	/**
	 * Sets the base Url of the loader
	 */
	public set baseUrl(url: string) {
		this._baseUrl = url;
		Loader._loaders.forEach((l) => (l.baseUrl = url));
	}

	/**
	 * Add the given file to the right loader
	 * @param file: the file to add to the proper loader
	 * @param key: optional, the key of the asset when calling get(key);
	 */
	public add(file: string | IAssetConf, key?: string, options?: any): void {
		if (typeof file !== "string") {
			return this._addFileAdvanced(file, key);
		}

		const extension = file.substring(file.lastIndexOf(".") + 1);
		let supported = false;
		let supportedLoader: ILoader = null;

		for (supportedLoader of Loader._loaders) {
			supported =
				supportedLoader.extensions.indexOf(extension) !== -1 ||
				supportedLoader.extensions.length === 0;
			if (supported) {
				break;
			}
		}

		if (supported) {
			supportedLoader.add(file, key || file, options);
		} else {
			throw new Error(`No loader found for extension "${extension}"`);
		}

		this.filesCount++;
	}

	/**
	 * Clears all the loaders
	 */
	public clear(): void {
		this.filesCount = 0;

		Loader._loaders.forEach((l) => {
			if (l.clear) {
				l.clear();
			}
		});
	}

	/**
	 * Removes the given entry identified by the given key
	 * @param key the key used to identify the entry to remove
	 */
	public remove(key: string): void {
		Loader._loaders.forEach((l) => l.remove && l.remove(key));
	}

	/**
	 * Calls all loaders
	 * @param callback called when all loaders have loaded their files
	 */
	public async load(callback?: () => void): Promise<void> {
		return new Promise<void>((resolve, reject) => {
			const length = Loader._loaders.length;
			let loadersCount = 0;
			let filesCount = 0;

			for (const loader of Loader._loaders) {
				loader.load(
					() => {
						// All loaded
						loadersCount++;

						if (loadersCount === length) {
							if (callback) {
								callback();
							}

							resolve();
						}
					},
					(loadedFile) => {
						// Progress
						filesCount++;

						if (this.onProgress) {
							const percentage = Math.min((filesCount * 100) / this.filesCount, 100) >> 0; // remove comma
							this.onProgress(percentage, loadedFile);
						}
					},
					(fileError) => {
						// Error
						if (this.onError) {
							this.onError(fileError);
						}

						reject(fileError);
					}
				);
			}
		});
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public get<T>(key: string, raw?: boolean): T {
		return Loader.get<T>(key, raw);
	}

	public get loaders(): ILoader[] {
		return Loader.loaders;
	}

	/**
	 * Configures the loader from the current theme's base Url
	 * @param language the language configuration of the game (mandatory to know which locales to load)
	 * @param baseUrl the base Url which contains the files.json file and all other files to load
	 * @param i18nBaseUrl the base Url where to find i18n files (not necessary the same folder or ../)
	 * @example loader.baseUrl = urlOfThemeFolder;
	 * @example loader.configureFromTheme("vi", "fr-FR");
	 * @example await loader.load();
	 * @example const locales = this.loader.get("vi-i18n-base"); // Get main locales (i18n)
	 * @example const overridenLocales = this.loader.get("vi-i18n"); // Get overriden locales (i18n)
	 */
	public async configureFromTheme(
		namespace: string,
		language: string,
		baseUrl: string = "",
		i18nBaseUrl: string = ""
	): Promise<void> {
		// Default behavior. Load files.json
		const tempLoader = new DefaultLoader();
		tempLoader.baseUrl = this.baseUrl;
		tempLoader.add(`${baseUrl}files.json`, "files-base");
		await tempLoader.load();

		// For each file, add to the loader
		const themeAssets = tempLoader.get<IAssetsConf>("files-base");

		for (const key in themeAssets) {
			if (!themeAssets.hasOwnProperty(key)) {
				continue;
			}

			const asset = themeAssets[key];

			if (key === "i18n") {
				this.add(baseUrl + asset[language], `${namespace}-i18n`);
			} else {
				// Just fonts, images, json, mp3, ogg, etc.
				if (typeof asset === "string") {
					this.add(baseUrl + asset, key);
				} else {
					this._addFileAdvanced(asset, key, baseUrl);
				}
			}
		}

		// Finally add the i18n file and load
		this.add(`${i18nBaseUrl}${language}.json`, `${namespace}-i18n-base`);
	}

	/**
	 * Parses the game conf and adds files to load (assets, i18n, etc.)
	 * @param conf the game configuration object to parse
	 */
	public loadFrom(conf: IGameConf, callback: () => void): void {
		if (!conf) {
			return;
		}

		// Theme
		const theme = conf.gameDescription.assets.themes[conf.selectedTheme];

		// Locales
		this.game.settings.language = conf.selectedLocale;

		// Load skin's file which defines all the files to load (images, fonts, etc.)
		const tempLoader = new DefaultLoader();
		tempLoader.baseUrl = this._baseUrl;
		tempLoader.add(`${this.assetsPath}${theme}/files.json`, "files-base");

		// Load skin description, then load the entire skin in the callback :)
		tempLoader.load(() => {
			// Default i18n files
			const currenti18n = conf.gameDescription.assets.i18n[conf.selectedLocale];
			if (currenti18n) {
				this.add(this.assetsPath + currenti18n, "i18n-base");
			}

			// Assets
			const themeAssets = tempLoader.get("files-base") as IAssetsConf;

			for (const key in themeAssets) {
				if (!themeAssets.hasOwnProperty(key)) {
					continue;
				}

				const asset = themeAssets[key];

				if (key === "version") {
					this.game.settings.versions.theme = themeAssets[key] as string;
				} else if (key === "i18n") {
					const file = asset[conf.selectedLocale];
					if (typeof file !== "string") {
						throw new LoaderError(
							`Expected file path to be set for locale "${conf.selectedLocale}"` +
								` (found <${typeof file}> instead of <string>;` +
								` did you forget to include your locale file` +
								` for locale "${conf.selectedLocale}" in your project` +
								`${theme !== "" ? ', for theme "' + theme + '"' : ""}` +
								` ?)`
						);
					}

					this.add(this.assetsPath + (theme === "" ? "" : `${theme}/`) + file, key);
				} else {
					// Just fonts, images, json, mp3, ogg, etc.
					if (typeof asset === "string") {
						this.add(this.assetsPath + (theme === "" ? "" : `${theme}/`) + asset, key);
					} else {
						this._addFileAdvanced(asset, key, theme);
					}
				}
			}

			// Load all assets! Then, config i18n tell that loading is done
			this.load(() => {
				this._i18nConfig(conf.selectedLocale);
				callback();
			});
		});
	}

	// Adds the file
	private _addFileAdvanced(asset: IAssetConf, key: string, theme: string = ""): void {
		// Internationalized?
		const language = this.game.settings.language;
		const assetPath = asset[language] || asset[language.toLowerCase()] || asset.default;

		if (!assetPath) {
			return;
		}
		this.add(
			this.assetsPath + (theme === "" ? "" : `${theme}/`) + assetPath,
			key || asset.default,
			asset.options
		);
	}

	// Configures the game
	private _i18nConfig(language: string): void {
		const locales = this.get<any>("i18n-base");
		if (locales.version) {
			this.game.settings.versions.i18n = locales.version as string;
		}

		i18n.addResourceBundle(language, "game", locales, true, true);

		const overridedLocales = this.get("i18n");
		if (overridedLocales) {
			i18n.addResourceBundle(language, "game", overridedLocales, true, true);
		}
	}
}

Loader.register(new DefaultLoader());
