import type { Howl } from "howler";

import { HowlerLoader } from "./howlerLoader";

export class SoundLayer {
	/**
	 * Returns all the sounds available in the given layer.
	 * @param layer the layer to get its sounds.
	 */
	public static getSoundsFromLayer(layer: number): Howl[] {
		const result: Howl[] = [];
		for (const s in HowlerLoader.sounds) {
			if (!HowlerLoader.sounds.hasOwnProperty(s)) {
				continue;
			}

			const ref = HowlerLoader.sounds[s];
			if (ref.layer === layer) {
				result.push(ref.data);
			}
		}

		return result;
	}

	/**
	 * Attach the given sound to the given layer.
	 * @param sound the sound reference to configure.
	 * @param layer the layer where to add the sound.
	 */
	public static setSoundLayer(sound: Howl, layer: number): void {
		for (const s in HowlerLoader.sounds) {
			if (!HowlerLoader.sounds.hasOwnProperty(s)) {
				continue;
			}

			const ref = HowlerLoader.sounds[s];
			if (ref.data === sound) {
				ref.layer = layer;
				return;
			}
		}
	}

	/**
	 * Sets the given layer muted or unmuted.
	 * @param layer the layer to mute or unmute.
	 * @param muted wether or not the layer should be muted.
	 */
	public static muteLayer(layer: number, muted: boolean): void {
		for (const s in HowlerLoader.sounds) {
			if (!HowlerLoader.sounds.hasOwnProperty(s)) {
				continue;
			}

			const ref = HowlerLoader.sounds[s];
			if (ref.layer === layer) {
				ref.data.mute(muted);
			}
		}
	}

	/**
	 * Sets the given layer paused or resumed.
	 * @param layer the layer to pause or resume.
	 * @param muted wether or not the layer should be paused or resumed.
	 */
	public static pauseLayer(layer: number, paused: boolean): void {
		for (const s in HowlerLoader.sounds) {
			if (!HowlerLoader.sounds.hasOwnProperty(s)) {
				continue;
			}

			const ref = HowlerLoader.sounds[s];
			if (ref.layer !== layer) {
				continue;
			}

			if (paused && ref.data.playing()) {
				ref.data.pause();
			} else if (!paused && !ref.data.playing()) {
				ref.data.play();
			}
		}
	}
}
