import { Numeral } from "./numeral";
import type { ICurrencyOptions } from "./numeral";

/**
 * Amount interface
 * value is a number given in the fraction digits of the currency
 * currency is an iso 4217 code
 */
export interface IAmount {
	value: number;
	currency: string;
}

/**
 * Amount manipulation
 * value is a number given in the fraction digits of the currency
 * currency is an iso 4217 code
 */
export class Amount implements IAmount {
	public value: number;
	public currency: string;

	/**
	 * Creates an amount object from an IAmount or a value.
	 * If only a number value is given, the currency code will be the Numeral one.
	 */
	public constructor(a: IAmount | number) {
		if (typeof a === "number") {
			this.value = a;
			this.currency = Numeral.currencyCode;
		} else {
			this.value = a.value;
			this.currency = a.currency;
		}
	}

	/**
	 * Multiplies an amount with a scalar
	 * @return a new amount with the operation result
	 */
	public multiply(scalar: number): Amount {
		return new Amount({ value: scalar * this.value, currency: this.currency } as IAmount);
	}

	/**
	 * Sum amounts
	 * @return a new amount with the operation result
	 */
	public sum(amount: IAmount): Amount {
		if (amount.currency !== this.currency) {
			throw new Error(`can't sum amount with inconsistent currencies.`);
		}
		return new Amount({ value: amount.value + this.value, currency: this.currency } as IAmount);
	}

	/**
	 * Currency formatting
	 * shortcut for Numeral.currency(amount, options)
	 */
	public toString(options?: ICurrencyOptions): string {
		return Numeral.currency(this, options);
	}

	/**
	 * Create an Amount object from a number
	 * @param value the value is a number given in the fraction digits of the currency
	 * the currencyCode will be the one of Numeral
	 */
	public static fromNumber(value: number): Amount {
		return new Amount({
			value,
			currency: Numeral.currencyCode,
		});
	}

	/**
	 * Format an amount value to string and show 2 digits if it's a whole number
	 * @param value: The value is a number given in the fraction digits of the currency
	 * @param options: Options to pass to the Numeral.currency function
	 * @return The formatted amount in string
	 */
	public static formatFromWholeOrRealNumber(value: number, options: ICurrencyOptions = {}): string {
		const minimumFractionDigits = (value / 100) % 1 > 0 ? 2 : 0;

		return Numeral.currency(Amount.fromNumber(value), {
			minimumFractionDigits,
			...options,
		});
	}
}
