import { EEventType } from "./event";
import type { IEvent } from "./event";

export enum ESlideEvent {
	Show = "show",
	Hide = "hide",
}

export interface ISlideEvent extends IEvent {
	event: ESlideEvent;
}

export class SlideEvent implements SlideEvent {
	public eventType: EEventType = EEventType.Slide;
	public constructor(public name: string, public event: ESlideEvent) {}
}
