import { tracker, EViewState, ViewStateEvent } from "../tracking";
import type { IView, ViewLifecycleHook } from "./view";

export interface IState {
	[index: string]: {
		next?: string;
		view: IView;
	};
}

export enum EStateEvents {
	Loaded = "stateloaded",
	Change = "statechange",
	Changed = "statechanged",
}

interface IEventEmitter {
	/**
	 * Calls each of the listeners registered for a givent event.
	 * @param {(string | symbol)} event The event name.
	 * @param {...*} args Arguments that are passed to registered listeners.
	 * @returns {boolean} `true` if the event had listeners, else `false`.
	 */
	emit(event: string | symbol, ...args: any[]): boolean;
}

export class State {
	// Public members
	public states: IState;

	public onLoaded: () => void;
	public onStateChange: (newState: string) => void;
	public onStateChanged: (state: string) => void;

	// Private members
	private _keys: string[];

	private _eventEmitter?: IEventEmitter;
	private _current: string;
	private _previous: string = null;
	private _currentView: IView = null;
	private _attrs: { [index: string]: any } = {};

	protected _busyOn: null | string = null;

	/**
	 * Constructor
	 * @param states: the states instance
	 */
	public constructor(states: IState) {
		this.states = states;

		this._keys = Object.keys(states);
		this._current = this._keys[0];
	}

	/**
	 * Set event emitter for state event notification.
	 * @param eventEmitter The event emitter.
	 */
	public set eventEmitter(eventEmitter: IEventEmitter) {
		this._eventEmitter = eventEmitter;
	}

	/**
	 * Get event emitter for state event notification.
	 */
	public get eventEmitter(): IEventEmitter {
		return this._eventEmitter;
	}

	/**
	 * Loads the views calling .onLoad
	 * @param view: specifies the first view to make appear
	 */
	public async load(view?: string): Promise<void> {
		await Promise.all(
			this._keys.map((k) => {
				const viewInstance = this.states[k].view;
				return this._executeViewLifecycleHook(viewInstance.onLoad, viewInstance);
			})
		);

		if (this.onLoaded) {
			this.onLoaded();
		}

		if (this._eventEmitter) {
			this._eventEmitter.emit(EStateEvents.Loaded);
		}

		await this.to(view || this._current);
	}

	/**
	 * Returns the current view name
	 */
	public current(): string {
		return this._current;
	}

	/**
	 * Returns the current view instance
	 */
	public currentView<T extends IView>(): T {
		return this.states[this._current].view as T;
	}

	/**
	 * Returns the previous view instance
	 * Note: returns null if no transition was done
	 */
	public previousView<T extends IView>(): T {
		return this.states[this._previous] ? (this.states[this._previous].view as T) : null;
	}

	/**
	 * Returns true if state is currently changing view (waiting for onWillAppear/onDidAppear)
	 * @returns boolean
	 */
	public get busy(): boolean {
		return this._busyOn !== null;
	}

	/**
	 * Switches to the given view
	 * @param view: the given view name
	 */
	public async to(view: string, next?: string): Promise<void> {
		const stepIndice = this._keys.indexOf(view);
		if (stepIndice === -1) {
			throw new Error(`View '${view}' does not exists`);
		}

		this._busyOn = view;

		if (next) {
			this.states[view].next = next;
		}

		if (this.onStateChange) {
			this.onStateChange(view);
		}

		if (this._eventEmitter) {
			this._eventEmitter.emit(EStateEvents.Change, view);
		}

		await this._switchCurrentView(this.states[view].view, view);

		if (this._busyOn === view) {
			this._busyOn = null;
		}
	}

	/**
	 * Switches to the next view
	 */
	public async next(): Promise<string> {
		const currentState = this.states[this._current];

		// If "next" is specified
		if (currentState.next) {
			await this.to(currentState.next);
			return currentState.next;
		}

		// Else simply go to the next view
		let currentIndice = this._keys.indexOf(this._current);

		if (currentIndice >= this._keys.length - 1) {
			currentIndice = 0;
		} else {
			currentIndice++;
		}

		const key = this._keys[currentIndice];
		await this.to(key);

		return key;
	}

	/**
	 * Executes onWillAppear and onDidAppear of the given view
	 * @param view the view to make appear
	 */
	public async executeAppear(view: IView, done?: () => void): Promise<void> {
		await this._executeCallback("onWillAppear", view);
		await this._executeCallback("onDidAppear", view);

		if (done) {
			done();
		}
	}

	/**
	 * Executes onWillDisappear and onDidDisappear of the given view
	 * @param view the view to make disappear
	 */
	public async executeDisappear(view: IView, done?: () => void): Promise<void> {
		await this._executeCallback("onWillDisappear", view);
		await this._executeCallback("onDidDisappear", view);

		if (done) {
			done();
		}
	}

	/**
	 * Get the value of the given key in attrs
	 * @param key the key to pick in attrs
	 * @param value the value to set
	 */
	public attrs<T>(key?: string, value?: T): T {
		if (key && value !== null && value !== undefined) {
			this._attrs[key] = value;
			return this._attrs[key];
		} else if (key !== null && key !== undefined) {
			return this._attrs[key];
		}

		return this._attrs as any;
	}

	/**
	 * Returns the states keys
	 */
	public stateKeys(): string[] {
		return this._keys.slice(0, this._keys.length);
	}

	/**
	 * Return the view associated to the given state
	 * @param key the state's key
	 */
	public getViewFromState<T extends IView>(key: string): T {
		for (const k of this._keys) {
			if (k === key) {
				return this.states[k].view as T;
			}
		}

		return null;
	}

	/**
	 * Return the state key associated to the given view
	 * @param view the view associated to a state
	 */
	public getStateFromView(view: IView): string {
		for (const k of this._keys) {
			if (this.states[k].view === view) {
				return k;
			}
		}

		return null;
	}

	// Switches the the new view calling the lifecycle old -> new
	private async _switchCurrentView(to: IView, key: string): Promise<void> {
		if (!this._currentView) {
			return await this._drawNewView(to, key);
		}

		// Close current view
		await this.executeDisappear(this._currentView);
		await this._drawNewView(to, key);
	}

	// Calls the lifecycle of the new view to switch on
	private async _drawNewView(view: IView, key: string): Promise<void> {
		if (this._currentView) {
			this._previous = this._current;
		}

		this._current = key;
		this._currentView = view;

		await this.executeAppear(view);
		if (this.onStateChanged) {
			this.onStateChanged(key);
		}

		if (this._eventEmitter) {
			this._eventEmitter.emit(EStateEvents.Changed, key);
		}
	}

	// Executes the lifecycle synchronously or asynchronously
	private _executeCallback(callbackName: string, context: IView): Promise<void> {
		const callback = context[callbackName] as ViewLifecycleHook;

		this._trackCall(callbackName); // track the state even if the view has not an overridden function

		if (!callback) {
			return Promise.resolve();
		}

		return this._executeViewLifecycleHook(callback, context);
	}

	/** Track an event if the callback match a state change */
	private _trackCall(callbackName: string): void {
		const eventName = `${callbackName.charAt(2).toLowerCase()}${callbackName.substring(
			3,
			callbackName.length
		)}`;
		for (const event of Object.keys(EViewState)) {
			if (eventName === EViewState[event]) {
				tracker.track(
					new ViewStateEvent(this._currentView.trackingName || this._current, eventName as any)
				);
				return;
			}
		}
	}

	/**
	 * Executes lifecycle hook synchronously or asynchronously
	 * @param hook ViewLifecycleHook.
	 * @param context context of the hook.
	 * @returns a promise resolved when the hook is done.
	 */
	private _executeViewLifecycleHook(hook: ViewLifecycleHook, context: IView): Promise<void> {
		const usesCallback = hook.length >= 1;
		if (usesCallback) {
			return new Promise((resolve) => hook.call(context, resolve));
		}

		const promise = hook.call(context) as Promise<void> | void;
		return promise instanceof Promise ? promise : Promise.resolve();
	}
}
