import { Tools } from "../tools";

/** Returns a new object that is a merge of object and fields.
 *  if object already contains fields, they are overided.
 */
export function mergeFields<A, B>(fields: A, object: B): A & B {
	const cloned = clone(object);
	return Tools.merge(fields, cloned);
}

/** Clone a given object */
export function clone<T>(object: T): T {
	return Tools.merge(object, {});
}
