import { Howl } from "howler";

import type { ILoader } from "../core";

export interface IFileType {
	/**
	 * The howler reference once the sound file has been loaded.
	 */
	data: Howl;
	/**
	 * The filename (path) of the sound file.
	 */
	filename: string;
	/**
	 * The layer where the sound is attached. -1 by default.
	 */
	layer: number;
}

export class HowlerLoader implements ILoader {
	// Public members
	public extensions: string[] = ["mp3", "ogg", "webm", "wav", "snd"];
	public baseUrl: string = "./";

	public static sounds: { [index: string]: IFileType } = {};

	/**
	 * Add the given file to the files to load
	 * @param filename the file to add
	 * @param key the key of the asset (works as a shortcut)
	 */
	public add(filename: string, key?: string): void {
		HowlerLoader.sounds[key || filename] = {
			filename,
			data: null,
			layer: -1,
		};
	}

	/**
	 * Clears the files to load dictionary
	 */
	public clear(): void {
		for (const file in HowlerLoader.sounds) {
			if (HowlerLoader.sounds[file].data) {
				HowlerLoader.sounds[file].data.unload();
			}
		}

		HowlerLoader.sounds = {};
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public get<T>(key: string): T {
		const value = HowlerLoader.sounds[key];
		if (!value) {
			return null;
		}

		return value.data as any;
	}

	/**
	 * Load all files previously added
	 * @param callback called when all files loaded
	 * @param progress called when a file has been loaded
	 * @param error called when an error happens
	 */
	public load(
		callback: () => void,
		progress?: (filename?: string) => void,
		error?: (file: string) => void
	): void {
		const keys = Object.keys(HowlerLoader.sounds);
		if (keys.length === 0) {
			return callback();
		}

		let count = 0;

		for (const key of keys) {
			const file = HowlerLoader.sounds[key];

			this._createInstance(
				file.filename,
				key,
				(loadedFile) => {
					count++;
					if (progress) {
						progress(loadedFile);
					}

					if (count === keys.length) {
						callback();
					}
				},
				(filename) => {
					if (error) {
						error(filename);
					}
				}
			);
		}
	}

	// Returns the possible sources of sound files to load according to
	// supported extensions
	private _getSrc(filename: string): string[] {
		const src: string[] = [];
		const extension = filename.substring(filename.lastIndexOf(".") + 1);
		const base = filename.replace(extension, "");

		// Add all supported extensions to src and let Howler just the needed one
		this.extensions.forEach((e) => src.push(this.baseUrl + base + e));

		return src;
	}

	// Creates a new instanceof of Howler sound
	private _createInstance(
		filename: string,
		key: string,
		callback: (filename: string) => void,
		error: (filename: string) => void
	): void {
		if (HowlerLoader.sounds[key].data) {
			return callback(filename);
		}

		const src = this._getSrc(filename);

		// Create new instance
		HowlerLoader.sounds[key].data = new Howl({
			src,
			onload: () => {
				callback(filename);
			},
			onloaderror: (soundId, err) => {
				// Howler already throws an error, don't need to throw here
				error(filename);
			},
		});
	}
}
