import { ELevel } from "./constants";
import type { IAdapter } from "./adapter";

export class ConsoleAdapter implements IAdapter {
	public constructor(private _level: ELevel) {}

	/**
	 * Log
	 * @param level specifies the level of log (debug/ingo/warn/error)
	 * @param data specifies the data to log
	 */
	public write(level: ELevel, ...data: any[]): void {
		const func = this._getFunc(level);

		if (!func) {
			return;
		}

		func.apply(console, data);
	}

	private _getFunc(level: ELevel): (...args: any[]) => void {
		if (level < this._level) {
			return undefined;
		}
		switch (level) {
			case ELevel.Debug:
				return console.log;
			case ELevel.Info:
				return console.info;
			case ELevel.Warn:
				return console.warn;
			case ELevel.Error:
				return console.error;
		}
	}
}
