import { Tools } from "../../tools";
import type { ILoader } from "./loader";

export interface IFileType {
	data: string;
	filename: string;
}

export class DefaultLoader implements ILoader {
	public extensions: string[] = [];
	public baseUrl: string = "";
	public filesToLoad: { [index: string]: IFileType } = {};

	/**
	 * Add the given file to the files to load
	 * @param filename the file to add
	 */
	public add(filename: string, key?: string): void {
		this.filesToLoad[key || filename] = {
			data: null,
			filename,
		};
	}

	/**
	 * Clears the files to load dictionary
	 */
	public clear(): void {
		this.filesToLoad = {};
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public get<T>(key: string): T {
		for (const file in this.filesToLoad) {
			if (file === key) {
				return this.filesToLoad[file].data as any;
			}
		}

		return null;
	}

	/**
	 * Load all files previously added
	 * @param callback called when all files loaded
	 */
	public async load(
		callback?: () => void,
		progress?: (filename?: string) => void,
		error?: (file: string) => void
	): Promise<void> {
		return new Promise<void>((resolve, reject) => {
			const length = Object.keys(this.filesToLoad).length;
			if (length === 0) {
				return callback();
			}

			let count = 0;

			for (const key in this.filesToLoad) {
				if (!this.filesToLoad.hasOwnProperty(key)) {
					continue;
				}

				const file = this.filesToLoad[key];

				this.loadFile(
					file.filename,
					key,
					false,
					(loadedFile) => {
						count++;
						if (progress) {
							progress(loadedFile);
						}

						if (count === length) {
							if (callback) {
								callback();
							}

							resolve();
						}
					},
					(fileWithError) => {
						if (error) {
							error(fileWithError);
						}

						reject(fileWithError);
					}
				);
			}
		});
	}

	// Loads the given file and callback when done
	protected loadFile(
		filename: string,
		key: string,
		useArrayBuffer: boolean,
		callback: (filename: string) => void,
		error: (file: string) => void
	): void {
		// If file alredy exists
		if (this.filesToLoad[key].data) {
			return callback(filename);
		}

		// Send request
		const request = new XMLHttpRequest();
		request.open("GET", this.baseUrl + filename, true);

		if (useArrayBuffer) {
			request.responseType = "arraybuffer";
		}

		request.onreadystatechange = () => {
			if (request.readyState !== (XMLHttpRequest.DONE || 4)) {
				return;
			}

			request.onreadystatechange = null;
			if (request.status >= 200 && request.status < 300) {
				if (Tools.getExtension(filename) === "json") {
					this.filesToLoad[key].data = JSON.parse(request.responseText);
				} else {
					this.filesToLoad[key].data = request.responseText;
				}

				callback(filename);
			} else {
				error(filename);
				throw new Error(`Error status: ${request.status} - Unable to load ${request.responseURL}`);
			}
		};

		request.send();
	}
}
