import { EEventType } from "./event";
import type { IEvent } from "./event";

export interface IGameInfo {
	technicalName: string;
	gameType: string;
	locale: string;
	currency: string;
	theme: string;
}

export enum EGameEvent {
	Loaded = "loaded",
	Quit = "quit",
}

export interface IGameEvent extends IEvent {
	event: EGameEvent;
}

export class GameLoaded implements IGameEvent {
	public eventType: EEventType = EEventType.Game;
	public event: EGameEvent = EGameEvent.Loaded;

	public constructor(public gameInfo: IGameInfo) {}
}

export class GameQuit implements IGameEvent {
	public eventType: EEventType = EEventType.Game;
	public event: EGameEvent = EGameEvent.Quit;
}
