// Got this from MDN:
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Number/toLocaleString#Example:_Checking_for_support_for_locales_and_options_arguments

import { option } from "yargs";

import type { ICurrencyOptions } from "./numeral";

function toLocaleStringSupportsLocales(): boolean {
	const number = 0;
	try {
		number.toLocaleString("i");
	} catch (e) {
		return e.name === "RangeError";
	}
	return false;
}

function replaceSeparators(
	sNum: string,
	separators: { thousands: string; decimal: string }
): string {
	const sNumParts = sNum.split(".");
	if (separators && separators.thousands) {
		sNumParts[0] = sNumParts[0].replace(/(\d)(?=(\d\d\d)+(?!\d))/g, `$1${separators.thousands}`);
	}
	sNum = sNumParts.join(separators.decimal);

	return sNum;
}

function renderFormat(template: string, props: object): string {
	for (const prop in props) {
		if (!props.hasOwnProperty(prop)) {
			continue;
		}
		template = template.replace(`{{${prop}}}`, props[prop]);
	}

	return template;
}

function mapMatch(map: any, locale: any): any {
	let match = locale;
	const language = locale && locale.toLowerCase().match(/^\w+/);

	if (!map.hasOwnProperty(locale)) {
		if (map.hasOwnProperty(language)) {
			match = language;
		} else {
			match = "en";
		}
	}

	return map[match];
}

function dotThousCommaDec(sNum: string): string {
	const separators = {
		decimal: ",",
		thousands: ".",
	};

	return replaceSeparators(sNum, separators);
}

function commaThousDotDec(sNum: string): string {
	const separators = {
		decimal: ".",
		thousands: ",",
	};

	return replaceSeparators(sNum, separators);
}

function spaceThousCommaDec(sNum: string): string {
	const seperators = {
		decimal: ",",
		thousands: "\u00A0",
	};

	return replaceSeparators(sNum, seperators);
}

function spaceThousButCommaDec(sNum: string): string {
	const sNumParts = sNum.split(".");
	if (sNumParts[0].length > 4) {
		return spaceThousCommaDec(sNum);
	}

	const seperators = {
		decimal: ",",
		thousands: "",
	};

	return replaceSeparators(sNum, seperators);
}

function apostrophThousDotDec(sNum: string): string {
	const seperators = {
		decimal: ".",
		thousands: "\u0027",
	};

	return replaceSeparators(sNum, seperators);
}

