//  "\xa0" is a non breaking space
export const currencyFormats = {
	pre: "{{code}}{{num}}",
	post: "{{num}}{{code}}",
	postspace: "{{num}}" + "\xa0" + "{{code}}",
	prespace: "{{code}}" + "\xa0" + "{{num}}",
	onlynum: "{{num}}",
};

export function toLocaleString(value: number, locale: string, options: any): string {
	if (locale && locale.length < 2) {
		throw new RangeError(`Invalid language tag: ${locale}`);
	}
	// exemple values are (150000, fr-CH ...) // (150001, fr-CH ...)
	// value example = 150000 // 150001

	value = value / Math.pow(10, digits[options.currency.toLowerCase()]);
	// value example = 1500 // 1500.01

	const scaleOrder = Math.pow(10, options.minimumFractionDigits);
	value = Math.round(value * scaleOrder) / scaleOrder;
	// value example = 1500 // 1500.01 (round depending on minimumFractionDigits, no change on those examples)

	let sNum: string = value.toFixed(options.minimumFractionDigits);
	// sNum example = "1500.00"  // "1500.01"

	sNum = mapMatch(transformForLocale, locale)(sNum, options);
	// sNum example = "1'500.00"  // "1'500.01"

	if (options && options.currency && options.style === "currency") {
		if (value % 1 === 0 && options.alternativeDecimalSymbol !== null) {
			// alternative decimal used
			if (options.minimumFractionDigits > 0) {
				// delete the decimals
				sNum = sNum.slice(0, -(options.minimumFractionDigits + 1));
				// sNum example = "1'500"  // "1'500.01"(unaffected)
			}

			sNum = sNum.concat(options.alternativeDecimalSymbol);
			// sNum example = "1'500.-"  // "1'500.01"(unaffected)
		}

		const format = currencyFormats[options.symbolFormatName ?? mapMatch(currencyFormatMap, locale)];
		// example format = "prespace"

		if (options.currencyDisplay === "code") {
			sNum = renderFormat(format, {
				num: sNum,
				code: options.currency.toUpperCase(),
			});
			// sNum example would be = "CHF 1'500.-" // "CHF 1'500.01"
		} else {
			const currencySymbol: string = currencySymbols[options.currency.toLowerCase()];

			sNum = renderFormat(format, {
				num: sNum,
				code: currencySymbol,
			});
			// CHF currency symbol is "CHF", so sNum example is = "CHF 1'500.-" // "CHF 1'500.01"
		}
	}
	return sNum;
}
