import { EEventType } from "./event";
import type { IEvent } from "./event";

export enum EViewState {
	WillDisappear = "willDisappear",
	DidDisappear = "didDisappear",
	WillAppear = "willAppear",
	DidAppear = "didAppear",
}

export enum EViewEvent {
	StateChange = "state",
}

export interface IViewInfo {
	name: string;
	state: EViewState;
}

export interface IViewEvent extends IEvent {
	name: string;
	event: EViewEvent;
}

export class ViewStateEvent implements IViewEvent {
	public eventType: EEventType = EEventType.View;
	public event: EViewEvent = EViewEvent.StateChange;
	public viewInfo: IViewInfo;

	public constructor(public name: string, state: EViewState) {
		this.viewInfo = { name, state };
	}
}
