import { ELevel, EOutput } from "./constants";
import { ConsoleAdapter } from "./consoleAdapter";
import type { IAdapter } from "./adapter";

/**
 * Logs library for game:
 *
 * @see Logger.init and Logger.getLogger for creating/getting instance
 *
 * Example:
 * In Game initialisation:
 *
 *   import { Logger, LEVEL, OUTPUT, TAG } from "../src/index";
 *
 *   Logger.init(LEVEL.info, OUTPUT.console);
 *
 * Wherever logger is needed:
 *    const logger = Logger.getLogger();
 *
 *    logger.log(LEVEL.info, "message");
 *
 *    logger.debug("warn");
 *
 *    logger.info("hello");
 *    logger.info(TAG, {}); // add TAG info in {}
 *
 *    logger.info("warn");
 *
 *    logger.error("error");
 *    logger.error(TAG, {}); // add TAG error params in {}
 *
 */
export class Logger {
	public static LEVEL: typeof ELevel = ELevel;
	public static OUTPUT: typeof EOutput = EOutput;

	/** Returns current logger (init must be called before)
	 * To log a message:
	 * logger.info('msg');
	 * To lag a TAG:
	 * logger.info(TAG, { TAG data })
	 */
	public static getLogger(): Logger {
		return Logger._logger;
	}

	/** Must be called when soft start to initiate the logger
	 * @param level: minimum level to log (default: LEVEL.info)
	 * @param outputs: where log will be send
	 */
	public static init(level: ELevel = ELevel.Info, ...outputs: EOutput[]): Logger {
		if (Logger._logger) {
			throw new Error("Logger is already instanciated.");
		}
		Logger._logger = new Logger(level, ...outputs);
		return Logger._logger;
	}

	private static _logger: Logger;

	private _adapters: IAdapter[] = [];

	private constructor(level: ELevel = ELevel.Info, ...outputs: EOutput[]) {
		if (!outputs.length) {
			outputs = [EOutput.Console];
		}
		for (const output of outputs) {
			switch (output) {
				case EOutput.Console:
					this.addLogger(new ConsoleAdapter(level));
					break;
				default:
					throw new Error(`Don't know this output ${output}`);
			}
		}
	}

	/**
	 * Add logger
	 * @param logger specifies logger to add
	 */
	public addLogger(logger: IAdapter): void {
		if (this._hasLogger(logger.constructor)) {
			return;
		}
		this._adapters.push(logger);
	}

	/**
	 * Log
	 * @param level specifies the level of log (debug/ingo/warn/error)
	 * @param data specifies the data to log
	 */
	public log(level: ELevel, ...data: any[]): void {
		for (const adapter of this._adapters) {
			adapter.write(level, ...data);
		}
	}

	/**
	 * Alias for log(ELevel.Debug, ...data);
	 * @param data specifies the data to log
	 */
	public debug(...data: any[]): void {
		this.log(ELevel.Debug, ...data);
	}

	/**
	 * Alias for log(ELevel.Info, ...data);
	 * @param data specifies the data to log
	 */
	public info(...data: any[]): void {
		this.log(ELevel.Info, ...data);
	}

	/**
	 * Alias for log(ELevel.Warn, ...data);
	 * @param data specifies the data to log
	 */
	public warn(...data: any[]): void {
		this.log(ELevel.Warn, ...data);
	}

	/**
	 * Alias for log(ELevel.Error, ...data);
	 * @param data specifies the data to log
	 */
	public error(...data: any[]): void {
		this.log(ELevel.Error, ...data);
	}

	private _hasLogger(type: any): boolean {
		const filtered = this._adapters.filter((adapter) => adapter instanceof type);
		return filtered.length !== 0;
	}
}
