// import Com from "@gdk/com";
import { mergeFields, clone } from "./tools";
import { Tools } from "../tools";
import { EEventType } from "./events/event";
import { EGameEvent } from "./events/game";
import { EViewEvent } from "./events/view";
import type { IGameInfo, IGameEvent, GameLoaded } from "./events/game";
import type { IEvent } from "./events/event";
import type { IViewInfo, IViewEvent, ViewStateEvent } from "./events/view";
import type { Logger } from "../logger";

export interface IComTracker {
	/**
	 * Trigger action on bridge
	 * @param action the action's name
	 * @param params parameters to send to the action
	 */
	trigger(action: string, params?: any): void;
}

export interface ITrackableObject {
	/**
	 * Defines the optional name of the object to be tracked.
	 * If unset, a default value will be taken by the tracker. For example,
	 * the name of the object (button, scratch, etc.).
	 */
	trackingName: string;
}

/** Tracker object allows to send tracked info to com layer. */
export class Tracker {
	private _com: IComTracker;
	private _logger: Logger;

	private _gameInfo: IGameInfo;
	private _viewInfo: IViewInfo;

	private _seqNum: number = 0;

	private _uuid: string;

	/** To be called once, at game initialization */
	public init({ com, logger }: { com: IComTracker; logger: Logger }): void {
		this._com = com;
		this._logger = logger;
		this._seqNum = 0;
		this._logger.debug("[TRACK]", "Tracking initialized");
		this._gameInfo = undefined;
		this._viewInfo = undefined;
		this._uuid = Tools.uuidv4();
	}

	/**
	 * track an event
	 * @param event specifies event to track
	 */
	public track(event: IEvent): void {
		switch (event.eventType) {
			case EEventType.Game:
				this._gameEvent(event as IGameEvent);
				break;
			case EEventType.View:
				this._viewEvent(event as IViewEvent);
				break;
			default:
				const data = this._getFullInfo(event);
				this._actualTrack(data);
				break;
		}
	}

	/** To be called when applet is loaded or quit */
	private _gameEvent(gameEvent: IGameEvent): void {
		let data;

		if (gameEvent.event === EGameEvent.Loaded) {
			this._gameInfo = clone((gameEvent as GameLoaded).gameInfo);
			data = clone(gameEvent);
		} else {
			data = this._getFullInfo(gameEvent);
		}

		this._actualTrack(data);
	}

	/** To be called each time a view state change */
	private _viewEvent(viewEvent: IViewEvent): void {
		if (viewEvent.event === EViewEvent.StateChange) {
			this._viewInfo = clone((viewEvent as ViewStateEvent).viewInfo);
		}

		const data = clone(viewEvent);
		(data as any).gameInfo = this._gameInfo;

		this._actualTrack(data);
	}

	/** Actual tracking */
	private _actualTrack(data: any): void {
		if (this._com) {
			this._com.trigger("TAG", data);
		}
		this._seqNum++;
	}

	/* Add fields most events have. MUST NOT be used for Game and view events */
	private _getFullInfo(event: IEvent): any {
		const data = clone(event);
		return mergeFields(
			{
				gameInfo: this._gameInfo,
				viewInfo: this._viewInfo,
			},
			data
		);
	}
}
