import { EEventType } from "./event";
import type { IEvent } from "./event";

export enum CLICK_EVENT {
	click = "click",
	button = "buttonClick",
	link = "linkClick",
}

/** Generic click event */
export class ClickEvent implements IEvent {
	public eventType: EEventType = EEventType.Click;

	public constructor(public name: string, public event: CLICK_EVENT | string = CLICK_EVENT.click) {}
}

/** To Track click on a button */
export class ButtonClick extends ClickEvent {
	public eventType: EEventType = EEventType.Click;
	public constructor(name: string) {
		super(name, CLICK_EVENT.button);
	}
}

/** To Track link on a button */
export class LinkClick extends ClickEvent {
	public constructor(name: string, public url: string) {
		super(name, CLICK_EVENT.link);
	}
}
