import { MD5, deterministicStringify } from "../tools";
import { Settings } from "./settings";
import { Loader } from "./loader/loader";
import type { State } from "./state";
import type { IGameConf } from "./loader/gameConfigurationTypes";

export class GameCore {
	// Public members
	public state: State = null;
	public canvas: HTMLCanvasElement;
	public gameDiv: HTMLElement;
	public window: Window;
	public settings: Settings = new Settings();
	public loader: Loader = new Loader(this);
	public gameConfig: IGameConf;

	public metadata: any;

	public onBlur: () => void;
	public onFocus: () => void;

	/**
	 * Constructor
	 * @param window specifies the window to work on
	 * @param canvas specifies the canvas to work on
	 * @param createGameDiv if a game div should be created to put canvas in it
	 */
	public constructor(window?: Window, canvas?: HTMLCanvasElement, createGameDiv: boolean = true) {
		this.window = window;
		this.canvas = canvas;

		if (createGameDiv) {
			this.createGameDiv();
		}
	}

	/**
	 * Starts the game.
	 * @param view Specifies the first view to make appear.
	 * @returns A promise resolved when the game is loaded and started or void for backward compatibility.
	 */
	public start(view?: string): Promise<void> | void {
		// Bind events
		if (this.window) {
			this.window.addEventListener("blur", () => this._blur());
			this.window.addEventListener("focus", () => this._focus());
		}

		// Load here
		if (!this.state) {
			throw new Error("You must provide a state");
		}
		return this.state.load(view);
	}

	/**
	 * Requests fullscreen on browser
	 * @return true if fullscreen is possible, false if not available (i.e iPhones)
	 */
	public requestFullscreen(): boolean {
		if (!this.gameConfig.gameParameters.fullscreenEnabled) {
			return;
		}

		const requestFullscreen =
			this.gameDiv &&
			(this.gameDiv.requestFullscreen ||
				this.gameDiv.msRequestFullscreen ||
				this.gameDiv.webkitRequestFullscreen ||
				this.gameDiv.mozRequestFullScreen);

		if (!requestFullscreen) {
			return false;
		}

		requestFullscreen.call(this.gameDiv);

		return true;
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public resource<T>(key: string): T {
		return Loader.get<T>(key);
	}

	/**
	 * Displays information about a game: its version, gdk modules versions
	 */
	public async infos(): Promise<void> {
		// For logging purpose, @TODO we should use the logger for this.
		console.log(await this.getInfos());
	}

	/**
	 * Return the game's infos. Should be overidden to add
	 * more informations to log
	 */
	protected async getInfos(): Promise<any> {
		const paths: { [name: string]: string } = this.gameConfig.modules.paths;

		if (this.gameConfig.baseUrl) {
			SystemJS.config({ baseURL: this.gameConfig.baseUrl });
		}

		const infos: any = {
			gameInfo: {
				applet: await this._getGameVersion("@gdk/the-awesome-game"),
				modules: await this._getModulesVersions(paths),
			},
			envInfos: {},
		};

		infos.gameInfo.hash = {
			applet: MD5.init(deterministicStringify(infos.gameInfo.applet)),
			modules: MD5.init(deterministicStringify(infos.gameInfo.modules)),
		};

		infos.gameInfo.hash.global = MD5.init(deterministicStringify(infos.gameInfo.hash));

		return infos;
	}

	/**
	 * Create container div for the game
	 * Called by gameCore constructor
	 * Can be overrided
	 */
	protected createGameDiv(): void {
		this.gameDiv = document.createElement("div");
		this.gameDiv.setAttribute("id", "gdkGameDiv");
		this.gameDiv.setAttribute("ondragstart", "return false;");
		this.gameDiv.setAttribute("ondrop", "return false;");
		this.gameDiv.style.position = "fixed";
		this.gameDiv.style.top = "0";
		this.gameDiv.style.left = "0";
		document.body.appendChild(this.gameDiv);
	}

	// When the window/tab changed
	private _blur(): void {
		if (this.onBlur) {
			this.onBlur();
		}
	}

	// When the window/tabs has been focused
	private _focus(): void {
		if (this.onFocus) {
			this.onFocus();
		}
	}

	// Get version for a given module
	// @param name: name of the module
	private async _getVersion(name: string): Promise<string> {
		const normalizedUrl = await SystemJS.resolve(name);
		if (SystemJS.has(normalizedUrl)) {
			const gdkModule = await SystemJS.import(name);
			return gdkModule.VERSION;
		}
	}

	// Get version for gdk modules used in the applet except submodules
	// @param paths: object with names and paths to gdk modules
	private async _getModulesVersions(paths: {
		[name: string]: string;
	}): Promise<{ [name: string]: string }> {
		const modulesVersions = {};

		for (const key in paths) {
			// discard submodules matching pattern path/to/submodule
			if (key.indexOf("@gdk") === 0 && /\/.*\/.*$/.test(key) === false) {
				const version = await this._getVersion(key);
				modulesVersions[key] = version;
			}
		}

		return modulesVersions;
	}

	// Get the applet's version.
	// If the game is run locally, we can't know its version. Thus, return 'dev'.
	// @param name: game name
	private async _getGameVersion(name: string): Promise<string> {
		const gameVersion = await this._getVersion(name);
		return gameVersion || "dev";
	}
}
