export class Tools {
	/**
	 * Copies the source(s) members into the target
	 * @param target the target object
	 * @param sources the source object(s)
	 */

	public static assign<T, U>(target: T, source: U): T & U;
	public static assign<T, U, V>(target: T, source1: U, source2: V): T & U & V;
	public static assign<T, U, V, W>(target: T, source1: U, source2: V, source3: W): T & U & V & W;
	public static assign(target: object, ...sources: any[]): any;
	public static assign(target: object, ...sources: any[]): any {
		// Convert target to Object, even if it's null or undefined
		let to = Object(target);
		for (const source of sources) {
			to = this.merge(source, to);
		}
		return to;
	}

	/**
	 * Merges the target's members to the source members
	 * @param source the source object
	 * @param target the target object
	 */
	public static merge<T, U>(source: T, target: U): T & U {
		if (!source) {
			return target as T & U;
		}

		const keys = Object.keys(source);
		for (const key of keys) {
			if (Array.isArray(source[key])) {
				target[key] = [];
				source[key].forEach((v) => target[key].push(v));
			} else if (typeof source[key] === "object") {
				if (source[key] !== null) {
					target[key] = target[key] || {};
					this.merge(source[key], target[key]);
				} else {
					target[key] = null;
				}
			} else {
				target[key] = source[key];
			}
		}

		return target as T & U;
	}

	/**
	 * Returns a perfect clone of the given object.
	 * @param object the object to clone.
	 * @warning take care of cyclic references.
	 * @warning should never be a class, only simple objects.
	 */
	public static cloneObject<T>(object: T): T {
		return JSON.parse(JSON.stringify(object));
	}

	/**
	 * Returns the extension of the file
	 * @param filename the filename
	 */
	public static getExtension(filename: string): string {
		const lastDot = filename.lastIndexOf(".");
		return filename.substring(lastDot + 1);
	}

	/**
	 * Returns the filename without extension
	 * @param filename: the filename (path)
	 * @param withPath: if the return value should contain all path
	 */
	public static getFilenameWithoutExtension(filename: string, withPath?: boolean): string {
		const lastDot = filename.lastIndexOf(".");

		let lastSlash = filename.lastIndexOf("/");
		if (lastSlash === -1) {
			lastSlash = filename.lastIndexOf("\\"); // Windows
		}
		return filename.substring(withPath ? 0 : lastSlash + 1, lastDot);
	}

	/**
	 * Returns the filename
	 * @param filename: the complete filename with path
	 */
	public static getFilename(filename: string): string {
		return `${this.getFilenameWithoutExtension(filename, false)}.${this.getExtension(filename)}`;
	}

	/**
	 * Returns the folder name containing the given filename
	 * @param filename the filename
	 */
	public static getFolder(filename: string): string {
		const index = filename.lastIndexOf("/");
		if (index === -1) {
			return "./";
		}

		return filename.substr(0, index);
	}

	/**
	 * Returns the constructor name of the given object
	 * @param obj the object
	 */
	public static getConstructorName(obj: any): string {
		let ctrName = obj && obj.constructor ? obj.constructor.name : "";

		if (ctrName === "") {
			ctrName = typeof obj;
		}

		return ctrName;
	}

	/**
	 * Creates a new native popup
	 * @param url: the URL of the web page
	 * @param title: the title of the popup
	 * @param width: the with of the popup. 800px by default
	 * @param height: the height of the popup. 600px by default
	 */
	public static createPopup(
		url: string,
		title: string,
		width: number = 800,
		height: number = 600
	): Window {
		const features = [
			`width=${width}`,
			`height=${height}`,
			`top=${window.screenY + Math.max(window.outerHeight - height, 0) / 2}`,
			`left=${window.screenX + Math.max(window.outerWidth - width, 0) / 2}`,
			"status=no",
			"resizable=yes",
			"toolbar=no",
			"menubar=no",
			"scrollbars=yes",
		].join(",");

		const popup = window.open(url, title, features);
		popup.focus();

		return popup;
	}

	/**
	 * Generates an uuid. As it relies on Math.random it may not be truly unique,
	 * however it is strong enough for our needs.
	 */
	public static uuidv4(): string {
		return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, (c) => {
			const r = (Math.random() * 16) | 0;
			const v = c === "x" ? r : (r & 0x3) | 0x8;
			return v.toString(16);
		});
	}

	/**
	 * Returns the nearest power of two value
	 * @param value the value get the power of 2
	 * @param max the max value of the power of 2
	 */
	public static getPowerOfTwo(value: number, max: number = 8192): number {
		let count = 1;

		// Remove comma in order to get nearest power of two.
		// i.e: 32.154266 should return 32 :)
		value = value >> 0;

		while (count < max) {
			if (count >= value) {
				return count;
			}

			count = count << 1;
		}

		return count;
	}

	/**
	 * Loads a file given its URL
	 * @param url: the url of the file to load
	 * @param arrayBuffer: if the response should be an array buffer instead of a text
	 * @param callback: optional callback if not using promises
	 */
	public static async loadFile<T extends string | ArrayBuffer>(
		url: string,
		arrayBuffer: boolean,
		callback?: (data: T) => void
	): Promise<T> {
		return new Promise<T>((resolve, reject) => {
			const request = new XMLHttpRequest();
			request.open("GET", url, true);

			if (arrayBuffer) {
				request.responseType = "arraybuffer";
			}

			request.onreadystatechange = () => {
				// In case of undefined state in some browsers.
				if (request.readyState === (XMLHttpRequest.DONE || 4)) {
					// Some browsers have issues where onreadystatechange can be called multiple times with the same value
					request.onreadystatechange = () => undefined;

					if (request.status >= 200 && request.status < 300) {
						// Success
						const data = !arrayBuffer ? request.responseText : request.response;
						if (callback) {
							callback(data);
						}

						resolve(data);
					} else {
						// Failed
						reject(new Error(`Error status: ${request.status} - Unable to load ${url}`));
					}
				}
			};

			request.send(null);
		});
	}

	/**
	 * Injects the given script code (javascript) to the DOM
	 * @param code the code of the script
	 * @param name the name of the script
	 * @param namespace the namespace of the script (to help debugging in devtools)
	 */
	public static injectScript(
		code: string,
		name: string,
		namespace: string = "scripts"
	): HTMLScriptElement {
		// Get url
		let url = window.location.href.split("?")[0];
		url = `${url.replace(Tools.getFilename(url), "")}${namespace}/`;

		// Create script
		const script = document.createElement("script");
		script.type = "text/javascript";
		script.text = `${code}\n//# sourceURL=${url}${name.replace(/ /g, "")}.js\n`;
		document.head.appendChild(script);

		return script;
	}

	/**
	 * Imports the given script identified by its name or URL
	 * @param keyOrUrl the name of the script to import
	 * @param baseURL the base Url used to request the dependencies of the script
	 */
	public static async importScript<T>(keyOrUrl: string, baseURL?: string): Promise<T> {
		if (baseURL) {
			SystemJS.config({ baseURL });
		}

		const exports = await SystemJS.import(keyOrUrl);
		return exports;
	}

	/**
	 * Returns the already script loaded using Tools.importScript or Tools.injectScript in case of a SystemJS module
	 * @param key the key of the script that has been registered
	 */
	public static getScript<T>(key: string): T {
		return SystemJS.get(key);
	}
}
