/**
 * Function from https://github.com/substack/json-stable-stringify
 * @param obj
 * @param opts
 */
export function deterministicStringify(obj: object, opts: any = {}): string {
	if (typeof opts === "function") {
		opts = { cmp: opts };
	}
	let space = opts.space || "";
	if (typeof space === "number") {
		space = Array(space + 1).join(" ");
	}
	const cycles = typeof opts.cycles === "boolean" ? opts.cycles : false;
	const replacer = opts.replacer || ((key, value) => value);

	const cmp =
		opts.cmp &&
		((f) => {
			return (node) => {
				return (a, b) => {
					const aobj = { key: a, value: node[a] };
					const bobj = { key: b, value: node[b] };
					return f(aobj, bobj);
				};
			};
		})(opts.cmp);

	const seen = [];
	return (function stringify(parent: any, key: string, node: any, level: number): string {
		const indent = space ? `\n${new Array(level + 1).join(space)}` : ";";
		const colonSeparator = space ? ": " : ":";

		if (node && node.toJSON && typeof node.toJSON === "function") {
			node = node.toJSON();
		}

		node = replacer.call(parent, key, node);

		if (node === undefined) {
			return;
		}
		if (typeof node !== "object" || node === null) {
			return JSON.stringify(node);
		}
		if (Array.isArray(node)) {
			const out = [];
			for (let i = 0; i < node.length; i++) {
				const item =
					stringify(node as any, i.toString(), node[i], level + 1) || JSON.stringify(null);
				out.push(indent + space + item);
			}
			return `[${out.join(",")}${indent}]`;
		} else {
			if (seen.indexOf(node) !== -1) {
				if (cycles) {
					return JSON.stringify("__cycle__");
				}
				throw new TypeError("Converting circular structure to JSON");
			} else {
				seen.push(node);
			}

			const keys = Object.keys(node).sort(cmp && cmp(node));
			const out = [];
			for (const eachKey of keys) {
				const value = stringify(node, eachKey, node[eachKey], level + 1);

				if (!value) {
					continue;
				}

				const keyValue = JSON.stringify(eachKey) + colonSeparator + value;
				out.push(indent + space + keyValue);
			}
			seen.splice(seen.indexOf(node), 1);
			return `{${out.join(",")}${indent}}`;
		}
	})({ "": obj }, "", obj, 0);
}
